function getrmb(Num) {
  var isNegative = false; // 

  if (Num < 0) {
    Num = -Num;
    isNegative = true;
  }

  if (typeof Num == 'number') {
    Num = Num.toString();
  }

  for (var i = Num.length - 1; i >= 0; i--) {
    Num = Num.replace(",", ""); // money,

    Num = Num.replace(" ", ""); // money
  }

  Num = Num.replace("", ""); // 

  if (isNaN(Num)) {
    // 
    return;
  } // ------//


  var part = String(Num).split(".");
  var newchar = ""; // 

  for (var i = part[0].length - 1; i >= 0; i--) {
    if (part[0].length > 10) {
      alertWarning("");
      return "";
    } // 


    var tmpnewchar = "";
    var perchar = part[0].charAt(i);

    switch (perchar) {
      case "0":
        tmpnewchar = "" + tmpnewchar;
        break;

      case "1":
        tmpnewchar = "" + tmpnewchar;
        break;

      case "2":
        tmpnewchar = "" + tmpnewchar;
        break;

      case "3":
        tmpnewchar = "" + tmpnewchar;
        break;

      case "4":
        tmpnewchar = "" + tmpnewchar;
        break;

      case "5":
        tmpnewchar = "" + tmpnewchar;
        break;

      case "6":
        tmpnewchar = "" + tmpnewchar;
        break;

      case "7":
        tmpnewchar = "" + tmpnewchar;
        break;

      case "8":
        tmpnewchar = "" + tmpnewchar;
        break;

      case "9":
        tmpnewchar = "" + tmpnewchar;
        break;
    }

    switch (part[0].length - i - 1) {
      case 0:
        tmpnewchar = tmpnewchar + "";
        break;

      case 1:
        if (perchar != 0) tmpnewchar = tmpnewchar + "";
        break;

      case 2:
        if (perchar != 0) tmpnewchar = tmpnewchar + "";
        break;

      case 3:
        if (perchar != 0) tmpnewchar = tmpnewchar + "";
        break;

      case 4:
        tmpnewchar = tmpnewchar + "";
        break;

      case 5:
        if (perchar != 0) tmpnewchar = tmpnewchar + "";
        break;

      case 6:
        if (perchar != 0) tmpnewchar = tmpnewchar + "";
        break;

      case 7:
        if (perchar != 0) tmpnewchar = tmpnewchar + "";
        break;

      case 8:
        tmpnewchar = tmpnewchar + "";
        break;

      case 9:
        tmpnewchar = tmpnewchar + "";
        break;
    }

    newchar = tmpnewchar + newchar;
  } // 


  if (Num.indexOf(".") != -1) {
    if (part[1].length > 2) {
      alertWarning(",");
      part[1] = part[1].substr(0, 2);
    }

    for (var i = 0; i < part[1].length; i++) {
      tmpnewchar = "";
      perchar = part[1].charAt(i);

      switch (perchar) {
        case "0":
          tmpnewchar = "" + tmpnewchar;
          break;

        case "1":
          tmpnewchar = "" + tmpnewchar;
          break;

        case "2":
          tmpnewchar = "" + tmpnewchar;
          break;

        case "3":
          tmpnewchar = "" + tmpnewchar;
          break;

        case "4":
          tmpnewchar = "" + tmpnewchar;
          break;

        case "5":
          tmpnewchar = "" + tmpnewchar;
          break;

        case "6":
          tmpnewchar = "" + tmpnewchar;
          break;

        case "7":
          tmpnewchar = "" + tmpnewchar;
          break;

        case "8":
          tmpnewchar = "" + tmpnewchar;
          break;

        case "9":
          tmpnewchar = "" + tmpnewchar;
          break;
      }

      if (i == 0) tmpnewchar = tmpnewchar + "";
      if (i == 1) tmpnewchar = tmpnewchar + "";
      newchar = newchar + tmpnewchar;
    }
  } // 


  while (newchar.search("") != -1) newchar = newchar.replace("", "");

  newchar = newchar.replace("", "");
  newchar = newchar.replace("", "");
  newchar = newchar.replace("", "");
  newchar = newchar.replace("", "");
  newchar = newchar.replace("", "");
  newchar = newchar.replace("", "");

  if (newchar.charAt(newchar.length - 1) == "" || newchar.charAt(newchar.length - 1) == "") {
    newchar = newchar + "";
  }

  if (isNegative) {
    newchar = '' + newchar;
  }

  return newchar;
}

module.exports = {
  getrmb
};